@import url('https://fonts.googleapis.com/css?family=Merienda+One');
@import url('https://fonts.googleapis.com/css?family=Shrikhand');
@import url('https://fonts.googleapis.com/css?family=New+Rocker');

*{
   margin: 0; 
   padding: 0;
 }

html, body {
   width: 100%;
   height: 100%;
 }

 header, nav, footer{
  width: 100%;  
}

header, nav{
   height: 10%;
 }

 header{
  text-align: center;
 }

body{
  background-color: 	#A9A9A9;
}

#mainContainer{
  height: 70%;    
}

footer{
  font-size: 1.5em;
  font-family: 'New Rocker', cursive;
  text-shadow: 1px 1px #fff;
  text-align: center;
}

.displayNone{
  display: none;
}
 
 #gameTitle, #gameSubTitle{
   font-weight: bolder;
   font-family: 'New Rocker', cursive;
   text-align: center;  
 }
 
 #gameTitle{
   font-size: 6em;
   color: 		#1E90FF;
   -webkit-text-stroke: 1px 	#8A2BE2;
 }
 
 #gameSubTitle{
   font-size: 3em;
   color: 		rgb(180, 44, 20);
   -webkit-text-stroke: 1px 	#000;
 }   

 .startBtn{
   background-color: #008000;
   border: 1px solid  #008000;
   font-size: 1.5em;
   color: #fff;
   font-family: 'New Rocker', cursive;
   width: 10%;
   height: 10%;
   margin: 5% 0 0 45%;
 }

 .startBtn:active{
  box-shadow: inset 0 0 30px  #008000;
  background-color:  #0f0;
  color: #000;
 }


 @media screen and (max-width: 768px) {
  #gameTitle{
    font-size: 4em;
  }
  #gameSubTitle{
    font-size: 2em;
  }   

  .startBtn{
    background-color: #008000;
    border: 1px solid  #008000;
    font-size: 1.5em;
    color: #fff;
    font-family: 'New Rocker', cursive;
    width: 30%;
    height: 10%;
    margin: 15% 0 0 35%;
  }

  .startBtn:active{
    box-shadow: inset 0 0 30px  #008000;
    background-color:  #0f0;
    color: #000;
   }
}